.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;

    background-color: rgba(23, 61, 51, 0.25);
    backdrop-filter: blur(2px);
    -webkit-backdrop-filter: blur(2px);

    z-index: 1300;
}

@media screen and (min-width: 768px) {
    .backdrop {
        justify-content: right;
    }
}

.modal {
    box-sizing: border-box;
    position: relative;
    border-radius: 25px;
    width: 365px;
    height: auto;
    width: 320px;
    max-height: 701px;

    padding: 24px;
    margin: 36px 20px;

    background-color: #173d33;
    opacity: 0.85;
    backdrop-filter: blur(12.5px);
    -webkit-backdrop-filter: blur(12.5px);
}

.modal::before {
    content: "";
    position: absolute;
    top: 48px;
    left: 50%;
    transform: translateX(-50%);

    height: 1px;
    width: 272px;
    background-color: #ffffff;

    z-index: 2000;
}

@media screen and (min-width: 768px) {
    .modal {
        margin-top: 36px;
        margin-right: 30px;
    }
}

.closeBtn {
    padding: 0;
    display: flex;
    align-items: baseline;
    border: none;
    background-color: transparent;
    color: #ffffff;

    font-size: 16px;
}

.text {
    color: #ffffff;
    margin-left: 4px;

    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
}

.icon path {
    transition: stroke 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
}

.closeBtn:hover .text,
.closeBtn:focus .text,
.closeBtn:hover .icon path,
.closeBtn:focus .icon path {
    color: #97d28b;
    stroke: #97d28b;
}

@media screen and (min-width: 1280px) {
    .modal {
        margin-top: 24px;
        margin-right: 100px;
        width: 365px;
        max-height: 873px;
    }

    .modal::before {
        width: 317px;
    }
}