.form {
  width: 320px;

  padding: 36px 12px;

  background-color: #eaedf1;


  @media screen and (min-width: 360px) and (max-width: 479px) {
    width: 100%;
  }

  @media screen and (min-width: 480px) and (max-width: 767px) {
    width: 440px;
  }

  @media screen and (min-width: 768px) {
    width: 340px;
    padding-left: 24px;
    padding-right: 24px;
    margin-left: auto;
  }

  @media screen and (min-width: 1280px) {
    width: 596px;
    height: 623px;
    padding: 48px;
  }
}

.input {
  margin-bottom: 28px;
  width: 100%;
  background-color: inherit;
  border-bottom: 1px solid #97d28b;
}

.label {
  margin-bottom: 8px;

  @media screen and (min-width: 1280px) {
    margin-bottom: 12px;
  }
}

.textarea {
  width: 100%;
  height: 147px;
  border-bottom: 1px solid #97d28b;
  background-color: inherit;
  resize: none;

  @media screen and (min-width: 1280px) {
    height: 180px;
  }
}

.button {
  width: 99px;
  height: 39px;
  border-radius: 50px;
  border: 1px solid #97d28b;

  display: flex;
  align-items: center;
  justify-content: center;
  padding-left: 13px;
  padding-right: 3px;
  margin-left: auto;
  margin-top: 16px;

  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    background-color: #173d33;
    color: #97d28b;
  }

  @media screen and (min-width: 1280px) {
    margin-top: 32px;
  }
}

.buttonText {
  margin-right: 10px;
}